<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名"
                      v-model="currencyTrans.keyword">
            </el-input>
            <el-button class="filter-item" @click="conditionQuery" style="margin-left: 30px">查询
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    tooltip-effect="dark"
                    style="width: 100%;"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>

            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page"
                               :page-sizes="[10,20,30, 50]" :page-size="currencyTrans.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {getApproval, passApproval, cancelApproval, lockApproval, issueApproval} from '@/api/awaits';
    //启用禁用筛选条件
    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    const statuScope = [
        {key: 0, display_name: '0项确认'},
        {key: 1, display_name: '1项确认'},
        {key: 2, display_name: '2项确认'},
        {key: 3, display_name: '成功'},
        {key: 4, display_name: '用户撤销'},
    ];

    export default {
        data() {
            return {
                //搜索需求数据等
                allCoin,
                coinList: [],
                dateTime: '',
                tableData: [],
                total: null,

                lockWithdrawalVo: {
                    id: '',
                    status: ''
                }
                ,
                currencyTrans: {
                    keyword: '',
                    status: 3,
                    page: 1,
                    pageSize: 10
                }
                ,
                msg: '',
                listLoading:
                    true,
                tableHeader: [{
                    label: "单号id",
                    prop: "id",
                    width: 65
                }, {
                    label: "会员UID",
                    prop: "userId",
                    width: 65
                }, {
                    label: "登录名",
                    prop: "phoneNum",
                    width: 100
                }, {
                    label: "会员昵称",
                    prop: "nickName",
                    width: 125
                }, {
                    label: "真实姓名",
                    prop: "currencyName",
                    width: 110
                }, {
                    label: "状态",
                    prop: "accountStatus",
                    width: 125,
                    align: 'center'
                }, {
                    label: "金额",
                    prop: "moneyNum",
                    width: 95,
                    align: 'center'
                }, {
                    label: "手续费",
                    prop: "chargeFee",
                    width: 110
                }, {
                    label: "银行",
                    prop: "bankName",
                    width: 120,
                    align: 'center'
                }, {
                    label: "提现账号",
                    prop: "account",
                    width: 220
                }, {
                    label: "创建时间",
                    prop: "createTime",
                    width: 170,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }, {
                    label: "最后修改时间",
                    prop: "updateTime",
                    width: 170,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }],
                multipleSelection: [],
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;

            }
        }
        ,
        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection[0].id);
            }
            ,
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                this.getList();
            },
            /**
             * 移入问题列表方法
             * @param id
             * @param status
             */
            issueApproval(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                issueApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已移入问题列表";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = "移入问题列表失败";
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 锁定订单方法
             * @param id
             * @param status
             */
            lockApproval(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                lockApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已锁定此单";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = "未能锁定请检查";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 取消提现方法
             * @param id
             */
            cancelApproval(id) {
                this.lockWithdrawalVo.id = id;
                cancelApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已取消提现";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 审核通过方法
             * @param id
             * @param status
             */
            passApproval(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                passApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "审核通过";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })

            }
            ,
            /**
             * 请求列表方法
             */
            getList() {
                this.listLoading = true;
                getApproval(this.currencyTrans).then(response => {
                    this.tableData = response.data.list;
                    this.total = response.data.total;
                    this.coinList.unshift(this.allCoin);
                    if (this.tableData.length === 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            }
        },
        created() {
            this.getList();
        }
    }
</script>



